.video {
  position: relative;
  padding-top: 91px;
  background: linear-gradient(180deg, #131314 87.8%, #ffffff 12.2%); }
  @media (max-width: 1023px) {
    .video {
      padding-top: 46px;
      background: linear-gradient(180deg, #131314 82.9%, #ffffff 17.1%); } }
  @media (max-width: 767px) {
    .video {
      padding-top: 60px;
      background: linear-gradient(180deg, #131314 88.8%, #ffffff 11.2%); } }

.video__text {
  max-width: 350px;
  margin: 0 auto;
  padding-bottom: 39px;
  text-align: center;
  color: #ffffff;
  letter-spacing: -0.005em; }
  @media (max-width: 1023px) {
    .video__text {
      max-width: 429px;
      padding-bottom: 23px; } }
  @media (max-width: 767px) {
    .video__text {
      max-width: 288px;
      padding-bottom: 37px; } }

.video__text h3 {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px; }
  @media (max-width: 1023px) {
