.main-nav__toggle {
  position: absolute;
  width: 50px;
  height: 50px;
  top: 0;
  right: 0;
  background-color: #0ad9c6;
  cursor: pointer;
  border: none;
  display: none; }
  .main-nav__toggle::before {
    content: "";
    position: absolute;
    width: 30px;
    height: 30px;
    top: 10px;
    left: 10px;
    background-image: url("../img/menu-icon.svg");
    background-repeat: no-repeat; }
  @media (max-width: 1023px) {
    .main-nav__toggle {
      display: flex; } }

.page-header--nojs .main-nav__toggle {
  display: none; }

.page-header--opened .main-nav__toggle {
  height: 52px;
  background-color: #ffffff; }
  .page-header--opened .main-nav__toggle::before {
    content: "";
    position: absolute;
    width: 26px;
    height: 26px;
    background-image: url("../img/cross.svg"); }
